.signup-container {
    display: flex;
    height: 100vh; /* Full viewport height */
}

.logo-container {
    flex: 1; /* Left half for the logo */
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('/logo.png'); /* Reference logo in the public folder */
    background-size: cover; /* Cover the entire area */
    background-position: center; /* Center the logo image */
    background-color: #f0f0f0; /* Fallback background color */
}

.form-container {
    flex: 1; /* Right half for the sign-up form */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px; /* Optional: add padding */
}

form {
    width: 100%; /* Full width for the form */
    max-width: 400px; /* Max width for the form */
    background: rgba(255, 255, 255, 0.8); /* Semi-transparent background */
    padding: 20px; /* Add some padding */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Shadow for depth */
}

input {
    width: 100%;
    padding: 10px;
    margin: 10px 0; /* Space between inputs */
}

button {
    width: 100%;
    padding: 10px;
    margin: 10px 0; /* Space between buttons */
    cursor: pointer;
}

/* Styling for error message */
p {
    color: red; /* Error message color */
    text-align: center;
}

/* Styling for success message */
.success-message {
    color: green;
    font-weight: bold;
    margin-bottom: 10px;
    background-color: #e0ffe0; /* Light green background */
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    max-width: 400px; /* Align with form width */
    width: 100%;
    box-shadow: 0 0 10px rgba(0, 255, 0, 0.2); /* Light shadow for success */
    margin: 20px 0; /* Add space between form and message */
}

/* Hover effect for buttons */
button:hover {
    background-color: #4caf50; /* Change background color on hover */
    color: white; /* Change text color on hover */
}
