/* Container for the entire page */
.home-container {
  padding: 20px;
}

/* Styling the category list */
.category-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 columns grid */
  gap: 20px;
  margin: 20px 0;
}

/* Styling each category card */
.category-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  cursor: pointer;
  
}

.category-card:hover {
  transform: scale(1.05);
}

/* Styling the images in the categories */
.category-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-bottom: 10px;
}

/* Error message styling */
.error-message {
  color: red;
  font-weight: bold;
}

/* Food item display */
.item-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Adjust to 3 items per row */
  gap: 20px;
}

.food-item {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
}

.food-item img {
  width: 150px;
  height: 150px;
  object-fit: cover;
}

.food-item h4 {
  margin: 10px 0 5px;
}

.food-item p {
  margin: 5px 0;
}

.category-image {
  width: 100%; /* Full width */
  height: auto; /* Maintain aspect ratio */
  max-height: 1080px;
  object-fit: cover; /* Crop or scale the image while keeping its aspect ratio */
  border-radius: 8px;
}

